




















import Vue from 'vue';
import { mapGetters } from 'vuex';

import store from '@/store';

export default Vue.extend({
  name: 'Accept',
  computed: {
    ...mapGetters('step', [
      'step',
    ]),
    ...mapGetters('photo', [
      'base64',
    ]),
  },
  methods: {
    handleNextStep() {
      store.dispatch('step/nextStep');
    },
    handlePrevStep() {
      store.dispatch('step/prevStep');
    },
  },
});
