


















import Vue from 'vue';
import { mapGetters } from 'vuex';

import store from '@/store';

export default Vue.extend({
  name: 'Photo',
  computed: {
    ...mapGetters('stream', [
      'mediaStream',
    ]),
    ...mapGetters('step', [
      'step',
      'stepHint',
    ]),
  },
  methods: {
    handleNextStep() {
      store.dispatch('photo/takeSnapshot', (this.$refs.video as HTMLVideoElement));
      store.dispatch('step/nextStep');
    },
  },
  created(): void {
  },
  async mounted(): Promise<void> {
    const video: HTMLVideoElement = (this.$refs.video as HTMLVideoElement);

    const screenWidth = window.screen.width;
    const clipPathProperty: string = `ellipse(calc(${screenWidth}px / 2.5) calc(${screenWidth}px / 2) at center calc(50vh - 50px))`;
    video.style.clipPath = clipPathProperty;
    // @ts-ignore
    video.style.webkitClipPath = clipPathProperty;

    video.srcObject = this.mediaStream;
    await video.play();
  },
});
