



















import Vue from 'vue';
import { mapGetters } from 'vuex';

import store from '@/store';

export default Vue.extend({
  name: 'Description',
  computed: {
    ...mapGetters('step', [
      'step',
      'stepDescription',
    ]),
  },
  methods: {
    handleNextStep() {
      store.dispatch('step/nextStep');
    },
  },
});
