import Vue from 'vue';

interface Stream {
  isStreaming: boolean;
  mediaStream?: MediaStream;
}

const STREAM_CONSTRAINTS: MediaStreamConstraints = {
  audio: false,
  video: {
    facingMode: 'user',
  },
};

const state: Stream = {
  isStreaming: false,
};
const mutations = {
  ADD_STREAM: (state: Stream, stream: MediaStream) => {
    Vue.set(state, 'mediaStream', stream);
    state.isStreaming = true;
  },
  DESTROY: (state: Stream) => {
    if (state.mediaStream) {
      state.mediaStream.getTracks().forEach(mediaStream => mediaStream.stop());
      state.isStreaming = false;
    } else {
      throw Error("Action: DESTROY. Camera didn't initialize!");
    }
  },
};
const actions = {
  init: async ({ commit }: {commit: Function}) => {
    const stream: MediaStream = await navigator.mediaDevices.getUserMedia(STREAM_CONSTRAINTS);
    commit('ADD_STREAM', stream);
    return stream;
  },
  destroy: ({ commit }: {commit: Function}) => {
    commit('DESTROY');
  },
};
const getters = {
  isStreaming: (state: Stream) => state.isStreaming,
  mediaStream: (state: Stream) => state.mediaStream,
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations,
};
